@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
*{
    margin: 0;
    padding: 0;
}
body{
    overflow: hidden;
}
.navbar{
    width: 100vw;
    height: 10vh;
    max-height: 10vh;
    min-height: 50px;
    background-color: #eee;
    overflow: hidden;
    padding-left: 1vw;
    user-select: none;
}
.nav-title{
    display: flex;
    flex-direction: row;
}
.nav-title>img{
    max-height: 100px;
    height:50px;
    min-width: 30px;
}
.nav-title>span{
    font-family: 'Noto Sans';
    font-size: 23px;
    line-height: 8vh;
    padding-left: 5px;
    vertical-align: middle;
}
.form-disp{
    width: 100vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif !important;
}
.form-container{
    width: 50%;
    height: auto;
    background-color: #eee;
    border-radius: 20px;
    padding: 50px;
}

.test-body{
    width: 100vw;
    height: 100vh;
    background-color: #eee;
    display: flex;
    justify-content: center;
}
.test-body form{
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.test-body form div:nth-child(2){
    height: auto;
}
.testPageDisp{
    display: flex;
    flex-direction: column;
}
.testPageDisp > textarea{
    height: 85vh;
    width: 100%;
    border-radius: 0;
    border: 0 solid #0000;
    resize: none;
    padding: 10px;
    outline: none;
    font-family: 'Noto Sans';
}   
.testPageDisp > button
{   
    height: 100%;
    border-radius: 0;
    font-family: 'Noto Sans';
}